import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ChooseLevelPage } from '../choose-level/choose-level';
import { StartGamePage } from '../start-game/start-game';

/**
 * Generated class for the GameOverPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */


@Component({
  selector: 'page-game-over',
  templateUrl: 'game-over.html',
})
export class GameOverPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GameOverPage');
  }
  PlayAgain(){
  	this.navCtrl.push(ChooseLevelPage);
  }
  Home(){
  	this.navCtrl.push(StartGamePage);
  }
}



