import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { MainRoomPage } from '../main-room/main-room';

/**
 * Generated class for the ChooseLevelPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */


@Component({
  selector: 'page-choose-level',
  templateUrl: 'choose-level.html',
})
export class ChooseLevelPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ChooseLevelPage');
  }
  Easy(){
    this.navCtrl.push(MainRoomPage, {difficulty:"Easy"});
  }
  Medium(){
    this.navCtrl.push(MainRoomPage, {difficulty:"Medium"});
  }
  Hard(){
    this.navCtrl.push(MainRoomPage, {difficulty:"Hard"});
  }

}



