import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { MainRoomPage } from '../main-room/main-room';
import { StartGamePage } from '../start-game/start-game';
import { YouWinPage } from '../you-win/you-win';

/**
 * Generated class for the EnterCodePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */


@Component({
  selector: 'page-enter-code',
  templateUrl: 'enter-code.html',
})
export class EnterCodePage {
	yourCode : any;
	mykeys : any;
	win : any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl : AlertController) {
  	this.mykeys = this.navParams.data.mykeys;
  	this.win = false;
  	this.yourCode = "00000000";
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EnterCodePage');
  }
    ButtonValue(number){
     if(this.yourCode.length<8){
        this.yourCode = this.yourCode+number;
        if(this.yourCode==this.navParams.data.gamewinningcode){
          this.win = true;
        	/*let alert = this.alertCtrl.create({
        		title : "You Win",
        		message : "You Have Won the Game!!",
        		buttons : [{
                text : "Play Again",
                role : "Play Again",
                handler : () =>{
                  this.navCtrl.push(MainRoomPage,{ playingagain : true});
                }
              },
              {
              	text : "Nah",
              	role : "Nah",
              	handler : () =>{
              		this.navCtrl.push(StartGamePage);
              	}
              }]
        	});
        	alert.present();*/
          this.navCtrl.push(YouWinPage);
        }
        else
        if(this.yourCode.length==8 && this.yourCode!=this.navParams.data.gamewinningcode)
        {
          let al = this.alertCtrl.create({
            title : "Oops",
            message : "Incorrect Code",
            buttons : [{
              text : "OK",
              role : "OK",
            }]
          });
          al.present();

        }
        
     }
     else{
     	this.yourCode=""+number;
     	console.log(this.yourCode);
     }
  }
}



