import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { ChooseLevelPage } from '../choose-level/choose-level';
import { InstructionsPage } from '../instructions/instructions';



@Component({
  selector: 'page-start-game',
  templateUrl: 'start-game.html',
})
export class StartGamePage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl : AlertController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad StartGamePage');
  }
  PlayGame(){
  	this.navCtrl.push(ChooseLevelPage);
  }
  HowToPlay(){
  	this.navCtrl.push(InstructionsPage);
  }
}



