import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { EnterCodePage } from '../enter-code/enter-code';
import { ButtonsPage } from '../buttons/buttons';
import { BoredButtonPage } from '../bored-button/bored-button';
import { SlidesPage } from '../slides/slides';

/**
 * Generated class for the MainRoomPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */


@Component({
  selector: 'page-main-room',
  templateUrl: 'main-room.html',
})
export class MainRoomPage {
  mykeys : any;
  numkeys : any;
  gamewinningcode : any;
  wins : any;
  difficulty : any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl : AlertController) {
    this.gamewinningcode = "";
    this.wins = [{"MockingButton": false}, {"Slides":false},{ "Buttons" : false}, {"Bored":false}];
    if(this.navParams.data.gamewinningcode){
      this.gamewinningcode = this.navParams.data.gamewinningcode;
    }
    if(this.navParams.data.wins){
      this.wins = this.navParams.data.wins;
    }
    else{
      for(var i=0; i<8; i++){
        this.gamewinningcode+= Math.ceil(Math.random()*9);
      }
    }
    console.log(this.gamewinningcode, ": the game winning code");
    console.log(this.wins);

    if(this.navParams.data.mykeys){
      this.mykeys = this.navParams.data.mykeys;
      this.numkeys = this.mykeys.length;
    }
    else
    if(this.navParams.data.playingagain==true){
      this.mykeys = ["HomeKey"];
      this.numkeys = this.mykeys.length;
    }
    else{
    this.mykeys = ["HomeKey"];
    this.numkeys = this.mykeys.length;
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MainRoomPage');
  }
  goToHome(){
    var found = false;
    for(var i=0; i<this.mykeys.length; i++){
      if(this.mykeys[i]=="HomeKey"){
        found = true;
      }
    }
    if(found==true){
      this.navCtrl.push(HomePage, {mykeys : this.mykeys, gamewinningcode : this.gamewinningcode, wins : this.wins, difficulty: this.navParams.data.difficulty});
    }
    if(found==false){
      let a = this.alertCtrl.create({
        title : "Oops",
        message : "You don't have the key to unlock this chest",
        buttons : [{text :"OK", role:"OK"}]
      });
      a.present();
    }
  }
  goToButtons(){
     var found = false;
    for(var i=0; i<this.mykeys.length; i++){
      if(this.mykeys[i]=="ButtonsKey"){
        found = true;
      }
    }
    if(found==true){
      this.navCtrl.push(ButtonsPage, {mykeys : this.mykeys, gamewinningcode : this.gamewinningcode, wins : this.wins, difficulty:this.navParams.data.difficulty});
    }
    if(found==false){
      let a = this.alertCtrl.create({
        title : "Oops",
        message : "You don't have the key to unlock this chest",
        buttons : [{text :"OK", role:"OK"}]
      });
      a.present();
    }
  }
  goToSlides(){
    var found = false;
    for(var i=0; i<this.mykeys.length; i++){
      if(this.mykeys[i]=="SlidesKey"){
        found = true;
      }
    }
    if(found==true){
      this.navCtrl.push(SlidesPage, {mykeys : this.mykeys, gamewinningcode : this.gamewinningcode, wins : this.wins, difficulty:this.navParams.data.difficulty});
    }
    if(found==false){
      let a = this.alertCtrl.create({
        title : "Oops",
        message : "You don't have the key to unlock this chest",
        buttons : [{text :"OK", role:"OK"}]
      });
      a.present();
    }

  }
  goToBoredButton(){
    var found = false;
    for(var i=0; i<this.mykeys.length; i++){
      if(this.mykeys[i]=="BoredKey"){
        found = true;
      }
    }
    if(found==true){
      this.navCtrl.push(BoredButtonPage, {mykeys : this.mykeys, gamewinningcode : this.gamewinningcode, wins : this.wins, difficulty:this.navParams.data.difficulty});
    }
    if(found==false){
      let a = this.alertCtrl.create({
        title : "Oops",
        message : "You don't have the key to unlock this chest",
        buttons : [{text :"OK", role:"OK"}]
      });
      a.present();
    }
  }
  EnterCode(){
    this.navCtrl.push(EnterCodePage, {mykeys : this.mykeys, gamewinningcode : this.gamewinningcode});
  }
}



