import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { StartGamePage } from '../start-game/start-game';

/**
 * Generated class for the WavingPandaPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */


@Component({
  selector: 'page-waving-panda',
  templateUrl: 'waving-panda.html',
})
export class WavingPandaPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  	this

  }

  ionViewDidLoad() {
    setTimeout(()=>{
      this.navCtrl.push(StartGamePage);
    },2000);
  }

}



