import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { StartGamePage } from '../start-game/start-game';
import { ChooseLevelPage } from '../choose-level/choose-level';

/**
 * Generated class for the YouWinPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */


@Component({
  selector: 'page-you-win',
  templateUrl: 'you-win.html',
})
export class YouWinPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad YouWinPage');
  }
  PlayAgain(){
  	this.navCtrl.push(ChooseLevelPage);
  }
  Home(){
  	this.navCtrl.push(StartGamePage);
  }
}



