import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { SlidesPage } from '../pages/slides/slides';
import { ButtonsPage } from '../pages/buttons/buttons';
import { MarqueePage } from '../pages/marquee/marquee';
import { MainRoomPage } from '../pages/main-room/main-room';
import { BoredButtonPage } from '../pages/bored-button/bored-button';
import { StartGamePage } from '../pages/start-game/start-game';
import { InstructionsPage } from '../pages/instructions/instructions';
import { EnterCodePage } from '../pages/enter-code/enter-code';
import { ChooseLevelPage } from '../pages/choose-level/choose-level';
import { GameOverPage} from '../pages/game-over/game-over';
import { YouWinPage } from '../pages/you-win/you-win';
import { WavingPandaPage } from '../pages/waving-panda/waving-panda';
@NgModule({
  declarations: [
    MyApp,
    HomePage,
    SlidesPage,
    ButtonsPage,
    MarqueePage,
    MainRoomPage,
    BoredButtonPage,
    StartGamePage,
    InstructionsPage,
    EnterCodePage,
    ChooseLevelPage,
    YouWinPage,
    GameOverPage,
    WavingPandaPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    SlidesPage,
    ButtonsPage,
    MarqueePage,
    MainRoomPage,
    BoredButtonPage,
    StartGamePage,
    InstructionsPage,
    EnterCodePage,
    ChooseLevelPage,
    YouWinPage,
    GameOverPage,
    WavingPandaPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



