import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { MainRoomPage } from '../main-room/main-room';
import { StartGamePage } from '../start-game/start-game';
import { GameOverPage } from '../game-over/game-over';

/**
 * Generated class for the BoredButtonPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */


@Component({
  selector: 'page-bored-button',
  templateUrl: 'bored-button.html',
})
export class BoredButtonPage {
  clicks : any
  timeleft : any;
  timeup : any;
  win : any;
  mykeys : any;
  partofcode : any;
  wins : any;
  clickgoal : any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl : AlertController) {
    this.wins = this.navParams.data.wins;
    this.mykeys = this.navParams.data.mykeys;
    this.partofcode = this.navParams.data.gamewinningcode.substring(6,8);
    if(this.navParams.data.wins[3]["Bored"]==true){
      let alert = this.alertCtrl.create({
                 title : 'Congrats!',
                 message : `<img src="assets/pandashortened.gif"><div>You win level 4! The final part of your code is : ${this.partofcode}</div>`,
                buttons : [{
                  text : "OK",
                  role : "OK",
                  handler : ()=>{
                    this.wins[3]["Bored"] = true;
                    this.navCtrl.push(MainRoomPage, { mykeys : this.mykeys, gamewinningcode : this.navParams.data.gamewinningcode, wins : this.wins, difficulty:this.navParams.data.difficulty});
                  }
                }],
                enableBackdropDismiss : false
                });
               alert.present();
    }
    else{
    if(this.navParams.data.difficulty==="Easy"){
      this.clickgoal = 50;
    }
    if(this.navParams.data.difficulty==="Medium"){
      this.clickgoal = 60;
    }
    if(this.navParams.data.difficulty==="Hard"){
      this.clickgoal = 75;
    }
    this.win = false;
    this.clicks = 0;
    this.timeleft = 10;
    this.timeup = false;
  	setInterval(()=>{
        
        if(this.timeup==false){
          if(this.win==true){
            this.timeleft = null;
          }
        	this.timeleft--;
        	if(this.timeleft==0){
        	  this.timeup = true;
        	  if(this.clicks>=this.clickgoal){
                this.win = true;
                this.timeleft = null;
                let alert = this.alertCtrl.create({
               	title : 'Congrats!',
               	message : `<img src="assets/pandashortened.gif"><div>You win level 4! The final part of your code is : ${this.partofcode}</div>`,
                buttons : [{
                  text : "OK",
                  role : "OK",
                  handler : ()=>{
                    this.wins[3]["Bored"] = true;
                    this.navCtrl.push(MainRoomPage, { mykeys : this.mykeys, gamewinningcode : this.navParams.data.gamewinningcode, wins : this.wins, difficulty:this.navParams.data.difficulty});
                  }
                }],
                enableBackdropDismiss : false
                });
               alert.present();
        	  }
        	  else
        	  if(this.clicks<this.clickgoal){
            this.navCtrl.push(GameOverPage);
        	 } 
           }
       }
  	},1000);
   }
  }

  ionViewDidLeave(){
    this.timeleft= null;
  }
  Click(){
  	if(this.timeup==false){
    this.clicks++;
   } 
  }
}



