import { Component } from '@angular/core';
import { Platform , AlertController} from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { WavingPandaPage } from '../pages/waving-panda/waving-panda';
import { StartGamePage } from '../pages/start-game/start-game';
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = WavingPandaPage;
  timeup : any;
  count : any;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, public alertCtrl : AlertController) {
    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
    });
    
  }
}




