import { Component, ViewChild } from '@angular/core';
import { NavController, AlertController, NavParams} from 'ionic-angular';
import { SlidesPage } from '../slides/slides';
import { ChooseLevelPage } from '../choose-level/choose-level';
import { MainRoomPage } from '../main-room/main-room';
import { StartGamePage } from '../start-game/start-game';
import { GameOverPage } from '../game-over/game-over';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
 
  showSomeShit : any 
  timeup : boolean;
  x : any;
  y : any;
  top: any;
  left : any;
  clicks : any;
  messages : any;
  count : any;
  message : any;
  mykeys : any;
  stop : any;
  partofcode : any;
  color: any;
  wins : any;
  alertPresented : any;
  goal : any;
  colors : any;
  colour : any;
  delay : any;
  constructor(public navCtrl: NavController, public alertCtrl : AlertController, public navParams : NavParams) {
      this.messages = ["Click me!", "Catch me", "Hahaha", "Too slow!", "Oop!", "Give up?", "Too fast huh?", "Muahaha","I'm right here!","Woohoo! Over here!"]
  	  this.showSomeShit = false;
      this.colors = ["danger", "light", "dark",""];
      this.partofcode = this.navParams.data.gamewinningcode.substring(0,2);
      this.mykeys = this.navParams.data.mykeys;
      this.wins = this.navParams.data.wins;
      if(this.navParams.data.wins[0]["MockingButton"]==true){
        let al = this.alertCtrl.create({
              title: "Congrats!",
              message : `<img src="assets/pandashortened.gif"><div>You win level 1! The first part of your code is : ${this.partofcode}</div>`,
              buttons : [{
                   text : "OK",
                   role : "OK",
                   handler : () =>{
                     this.wins[0]["MockingButton"] = true;
                     this.navCtrl.push(MainRoomPage, {mykeys : this.mykeys, gamewinningcode : this.navParams.data.gamewinningcode, wins : this.wins, difficulty:this.navParams.data.difficulty});
                   }
              }],
              enableBackdropDismiss : false
            });
            al.present();
      }
      else{
  	  this.count = 30;
  	  this.timeup = false;
      this.clicks = 0;
      this.alertPresented = false;
      this.stop = false;
      this.goal = 10;
      if(this.navParams.data.difficulty==="Easy"){
        this.delay = 700;
      }
      if(this.navParams.data.difficulty==="Medium"){
        this.delay = 600;
      }
      if(this.navParams.data.difficulty==="Hard"){
        this.delay = 500;
      }
      setInterval(()=>{
         this.left = Math.floor(Math.random()*400);
         this.top = Math.floor(Math.random()*400);
         this.colour = this.colors[Math.floor(Math.random()*4)];
         this.message = this.messages[Math.floor(Math.random()*10)];

      }, this.delay);
      setInterval(()=>{
      	  if(this.timeup==false){
          this.count--;
          if(this.count<=10){
            this.color="red";
          }
          if(this.count==0 && this.clicks<this.goal){
            this.navCtrl.push(GameOverPage);
          }
          if(this.clicks>=this.goal){
            this.count=null;
            if(this.alertPresented==false){
            let al = this.alertCtrl.create({
              title: "Congrats!",
              message : `<img src="assets/pandashortened.gif"><div>You win level 1! The first part of your code is : ${this.partofcode}</div>`,
              buttons : [{
                   text : 'OK',
                   role : 'OK',
                   handler : () =>{
                     this.mykeys.push("SlidesKey")
                     this.wins[0]["MockingButton"] = true;
                     this.navCtrl.push(MainRoomPage, {mykeys : this.mykeys, gamewinningcode : this.navParams.data.gamewinningcode, wins : this.wins, difficulty:this.navParams.data.difficulty});
                   }
              }],
              enableBackdropDismiss : false
            });
            al.present();
            this.alertPresented = true;
            }
          }

          } 
      },1000);
    }
  }
  ionViewDidLoad(){
    if(this.navParams.data.wins[0]["MockingButton"]==false){
     let a = this.alertCtrl.create({
       title : "Click the moving button "+this.goal+" Times, except when it's red!",
       message : `<img src="assets/pandashortened.gif">`,
       buttons : [{text: "OK", role : "OK"}]
     });
     a.present();
   }
  }
 
  goToSlides(){
  	this.navCtrl.push(SlidesPage);
  }
  Click(){
    if(this.colour=="danger"){
      this.navCtrl.push(GameOverPage);
    }
    else{
    this.clicks++;
    }
  }
  ionViewDidLeave(){
    this.count= null;
  }
}



